import React, {FC} from "react";
import './CardPublication.css';
import { useTranslation } from 'react-i18next';

interface ICardPublication {
    NameHeader:string;
    Description:string;
    ImgLink:string;
    Link:string;
}

export const CardPublication:FC<ICardPublication> = ({NameHeader, Description, ImgLink, Link}) => {
    const {t} = useTranslation();

    return(
        <a href={Link} className="card-publication">
            <div className="card-publication__content">
                <div>
                    <img src={ImgLink} alt=""/>
                </div>

                <div className="card-publication__text">
                    <h4>{t(NameHeader)}</h4>
                    <p>{t(Description)}</p>
                </div>
            </div>
        </a>
    )
}