import React, {FC} from 'react';
import {HeaderSection} from '../UI/MainSections/HeaderSetion/HeaderSection';
import './Geograph.css';
import { worldMap } from '../../assets';

export const Geograph: FC = () => {
  return (
    <section className='geograph' id="geograph">

        <HeaderSection
            header={'Components.UI.MainSection.HeaderSection.GeographyOfWorkAndProfessionalTrainings'}
        />

        
        <div className='geograph-map'>
            <div className='geograph-map-img'>
                <img src={worldMap} alt="" />
            </div>
        </div>

    </section>
  )
}
